.swh-browse-nav li a {
    -webkit-border-radius: 4px;
       -moz-border-radius: 4px;
            border-radius: 4px;
}

.scrollable-menu {
    max-height: 180px;
    overflow-x: hidden;
}

.swh-corner-ribbon {
    width: 200px;
    background: #fecd1b;
    color: #e20026;
    position: absolute;
    text-align: center;
    line-height: 50px;
    letter-spacing: 1px;
    -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
       -moz-box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
            box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    top: 55px;
    right: -50px;
    left: auto;
    -webkit-transform: rotate(45deg);
       -moz-transform: rotate(45deg);
         -o-transform: rotate(45deg);
            transform: rotate(45deg);
    z-index: 2000;
}

.swh-loading {
    display: none;
    text-align: center;
    margin-top: 10px;
}

.swh-loading.show {
    display: block;
}

.swh-metadata-table-row {
    border-top: 1px solid #ddd !important;
}

.swh-metadata-table-key {
    min-width: 200px;
    max-width: 200px;
    width: 200px;
}

.swh-metadata-table-value pre {
    white-space: pre-wrap;
}

.swh-table-even-odd th {
    border-top: none;
}

.swh-table-even-odd tr:nth-child(even) {
    background-color: #f5f5f5;
}

.swh-table-even-odd tr:nth-child(odd) {
    background-color: #fff;
}

.swh-table-cell-text-overflow {
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.swh-directory-table {
    margin-bottom: 0;
}

.swh-directory-table td {
    border-top: 1px solid #ddd !important;
}

.swh-title-color {
    color: #e20026;
}

.swh-log-entry-message {
    min-width: 460px;
    max-width: 460px;
    width: 460px;
}

.swh-popover {
    max-height: 50vh;
    max-width: 80vw;
    overflow-y: auto;
    overflow-x: auto;
    padding: 0;
    padding-right: 1.4em;
}

.swh-search-pagination {
    margin-top: 5px;
}

.ui-slideouttab-panel {
    z-index: 30000;
}

#swh-identifiers {
    width: 70vw;
    top: 0;
    border: 1px solid #e20026;
}

#swh-identifiers .handle {
    background-color: #e20026;
    border: 1px solid #e20026;
    color: white;
    padding-top: 0.1em;
    padding-bottom: 0.1em;
}

#swh-identifiers-content {
    height: 100%;
    overflow: auto;
}
